import React from 'react'
import './speed.css'

const SpeedCar = (props) => {
    const style = {
        left: `${props.speedDots[0]}%`,
        top: `${props.speedDots[1]}%`
    }

    return (
        <div className="speed_car" style={style}>
            <div className="car_1"/>
            <div className="car_2"/>
            <div className="car_3"/>
            <div className="car_4"/>
        </div>
    )
}

export default SpeedCar;