import React from 'react'
import './games.css'
import Back from "./back";

const Games = (props) => {
    return (
        <>
            <Back close={props.toHome}/>
            <nav className='nav'>
                <h2>Choice Game</h2>
                <ul>
                    <li className="nav_item">
                        <div onClick={props.openSnake} className='nav_link'>Snake</div>
                    </li>
                    <li className="nav_item">
                        <div onClick={props.openCoh} className='nav_link'>Cold Or Hot</div>
                    </li>
                    <li className="nav_item">
                        <div onClick={props.openSpeed} className='nav_link'>Speed</div>
                    </li>
                </ul>
            </nav>
        </>
    )
}

export default Games;