import React, {Component} from 'react';
import Back from "../../back";
import './speed.css'
import SpeedCar from "./speed-car";
import SpeedRandom from "./speed-random";

const getRandomCoordinates = () => {

    // let min = 0;
    // let max = 3;
    // let x = Math.floor(Math.random() * (max - min)) + min;
    let x = Math.floor((Math.random() * 100) / 50);
    let y = 0;

    return [ x*50, y-25 ];

}


const initialState = {
    car: getRandomCoordinates(),
    speed: 50,
    direction: 'down',
    speedDots: [0, 75],
    score: []
}

class Speed extends Component {
    state = initialState

    componentDidMount() {
        setInterval(this.moveCar, this.state.speed);
        document.onkeydown = this.onKeyDown;
    }

    componentDidUpdate() {
        this.newCar();
        this.chekIfCrash()
    }

    onKeyDown = (event) => {
        event = event || window.event;
        // eslint-disable-next-line
        switch (event.keyCode) {
            case 37:
                this.setState({speedDots: [0, 75]})
                break;
            case 39:
                this.setState({speedDots: [50, 75]})
                break;
        }


    }

    moveCar = () => {
        let dots = [...this.state.car];
        this.setState({
            car: [dots[0], dots[1]+5]
        })
    }

    newCar(){
        const car = this.state.car[1];
        if (car > 90){
            this.setState({
                car: getRandomCoordinates()

            })
        }
    }
    chekIfCrash() {
        let stayCar = [...this.state.speedDots]
        let runCar = [...this.state.car]
        if (runCar[0] === stayCar[0] && runCar[1] === stayCar[1]-20 ) {
            this.onGameOver()
        }
    }

    onGameOver() {
        alert(`GAME OVER. Your score: error`)
        this.setState(initialState)
    }

    left() {
        this.setState({speedDots: [0, 75]})
    }
    right() {
        this.setState({speedDots: [50, 75]})
    }


    render() {
        return (
            <div className='speed_container'>
                <Back close={this.props.toBack}/>
                <div className="speed_area">
                    <SpeedCar speedDots={this.state.speedDots} />
                    <SpeedRandom dot={this.state.car} />

                </div>
                <div className="left_tab" onTouchStart={() => this.left()}/>
                <div className="right_tab" onTouchStart={() => this.right()}/>
            </div>
        )
    }
}

export default Speed;