import React from 'react'

const SpeedRandom = (props) => {
    const style = {
        left: `${props.dot[0]}%`,
        top: `${props.dot[1]}%`
    }

    return (
        <div className='speed_random' style={style}>
            <div className="car-r_1"/>
            <div className="car-r_2"/>
            <div className="car-r_3"/>
            <div className="car-r_4"/>
        </div>
    )
}

export default SpeedRandom;