import React, {Component} from "react";
import Home from "./home";
import Games from "./games";
import Snake from "./games/snake/snake";
import CoH from "./games/coldOrHot/coh";
import Speed from "./games/speed/speed";

// import './Layout.css';


class Layout extends Component {
    constructor(props) {
        super(props);

        this.state = {
            home: true,
            getStart: false,
            snake: false,
            coh: false,
            speed: false
        }
    }

    closeStart = () => {
        this.setState({
            home: false,
            getStart: true
        })
    }

    toSnake = () => {
        this.setState({
            getStart: false,
            snake: true,
            coh: false,
            speed: false
        })
    }

    toCoh = () => {
        this.setState({
            getStart: false,
            snake: false,
            coh: true,
            speed: false
        })
    }

    toSpeed = () => {
        this.setState({
            getStart: false,
            snake: false,
            coh: false,
            speed: true
        })
    }

    toHome = () => {
        this.setState({
            home: true,
            getStart: false
        })
    }
    toBack = () => {
        this.setState({
            getStart: true,
            snake: false,
            coh: false,
            speed: false
        })
    }

    render() {
        return (
            <div className='Layout'>
                {this.state.home && <Home onClose={this.closeStart}/>}
                {this.state.getStart && <Games
                    toHome={this.toHome}
                    openSnake={this.toSnake}
                    openCoh={this.toCoh}
                    openSpeed={this.toSpeed}
                />}
                {this.state.snake && <Snake toBack={this.toBack} />}
                {this.state.coh && <CoH toBack={this.toBack} />}
                {this.state.speed && <Speed toBack={this.toBack} />}
            </div>
        )
    }
}

export default Layout;