import React from 'react'
import './home.css'

const Home = (props) => {

        return (


                <div className="home_container">
                    <h1>Welcome to Game</h1>
                    <p>Please press Get Started to select a game</p>
                    <div className='btn_start' onClick={props.onClose}>
                        GET STARTED
                    </div>
                </div>

        )
    }

export default Home;